<template>
  <aside class="GlobalSideBar">
    <div class="Card GlobalWrite"></div>
    <div class="Card GlobalWrite"></div>
    <div class="Card GlobalWrite"></div>
    <div class="Card GlobalWrite"></div>
    <div
      id="fixedId"
      class="Card GlobalWrite"
      :class="{'is-fixed':isFixed}" :style="getFixedStyle"
      ref="fixedBlock">固定
    </div>
  </aside>
</template>

<script>
  export default {
    name: "AsideBasic",
    data() {
      return {
        isFixed: false,
        offsetTop: 0,
      }
    },
    computed: {
      // 计算属性的 getter
      getFixedStyle() {
        if (this.isFixed) {
          // `this` 指向 vm 实例
          const domReact = this.$refs.fixedBlock.getBoundingClientRect();
          return 'top: 0;left: ' + domReact.left + 'px;width: 296px;'
        } else {
          return {}
        }
      }
    },
    mounted() {
      this.offsetTop = document.querySelector('#fixedId').offsetTop;
      window.addEventListener('scroll', this.handleScroll);
      this.$once('hook:beforeDestroy', () => {
        window.removeEventListener('scroll', this.handleScroll);
      })
    },
    methods: {
      handleScroll() {
        // console.log('handleScroll', this.$refs.fixedBlock.getBoundingClientRect());
        const scrollTop = window.pageYOffset || document.documentElement.scrollTop || document.body.scrollTop;
        // const offsetTop = document.querySelector('#fixedId').offsetTop
        // console.log('handleScroll', scrollTop, this.offsetTop, offsetTop);
        if (scrollTop > this.offsetTop) {
          this.isFixed = true
        } else {
          this.isFixed = false
        }
      },
    },
  }
</script>

<style lang="less" scoped>
  .GlobalSideBar {
    flex: 1 1;
    font-size: 14px;
    background: lightpink;

    .Card {
      margin-bottom: 10px;
      background: #fff;
      overflow: hidden;
      border-radius: 2px;
      box-shadow: 0 1px 3px rgba(26, 26, 26, 0.1);
      box-sizing: border-box;

      &.GlobalWrite {
        display: flex;
        height: 188px;
        flex-wrap: wrap;
        overflow: visible;
        background: lightblue;
      }
    }
  }
</style>